.skew--block {
  overflow: hidden;
  position: relative;
  width: 288px;
  height: 40px;
  align-items: center;
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  transform: skew(-25deg, 0deg);
  transition: .5s;
}

.skew--block::after {
  content: "";
  width: 0;
  height: 120%;
  position: absolute;
  bottom: 0;
  left: -10%;
  background-color: #00ADEF;
  transform: skew(-25deg, 0deg);
  z-index: 1;
  transition: .5s;
}

.skew--block:hover {
  cursor: pointer;
}

